<template>
	<div>
		<van-nav-bar fixed @click-left="leftclick" title="旅客信息详情" left-text="返回" right-text="编辑" left-arrow @click-right="edit" />

		<div class="content">
            <van-cell  title="当前房号"  style="font-weight: bold;" class="fang" :value="list.F_RoomNumber" />
			<div style="padding: 1rem;">
				<img class="content_1" @click="look(index,headimg)" :src="form.photo" style="width:5rem;height:rem" alt="">
				<van-image @click="look(index, headimg)" class="content_1" v-for="(item, index) in headimg" :key="index" width="5rem" height="5rem" fit="contain" :src="item" />
			</div>

			<van-cell-group>
				<van-cell required title="姓名" :value="list.F_Name" />

                <van-cell required :title="list.F_IDCardType==0?'身份证':list.F_IDCardType==1?'护照':list.F_IDCardType==2?'外国人永久居留证':list.F_IDCardType==3?'港澳通行证':''" :value="list.F_IDCard" />

				<van-cell required title="手机号">
					<template #extra>
						<div style="display: flex;margin-left: auto;" @click="callPhone(form.F_MobileTelphone)">
							<div><van-icon name="phone-o" size="18" style="top:0.14rem;" /></div>
							<div>
								<span style="color:#4169E1;">{{ list.F_MobilePhone }}</span>
							</div>
						</div>
					</template>
				</van-cell>

		
				<van-cell  title="性别" :value="list.F_Sex" />
				<van-cell  title="民族" :value="list.F_Nation" />
				<van-cell  title="户籍住址" :value="list.F_HouseholdRegisterAddress" />



				<van-cell required title="来访地风险等级" class="xian" :value="(list.F_IsFromLowRiskArea?'-低-':'')+(list.F_IsFromMiddleRiskArea?'中':'')+(list.F_IsFromHighRiskArea?'-高-':'')" />
				<van-cell required title="中风险来访地" v-if="list.F_MiddleRiskComeArea!=''" class="xian" :value="list.F_MiddleRiskComeArea" />
		
				<van-cell required title="高风险来访地" v-if="list.F_HighRiskComeArea!=''" class="xian" :value="list.F_HighRiskComeArea" />



				<van-cell required title="体温是否正常" v-if="!list.F_TemperatureIsNormal" :value="list.F_TemperatureIsNormal?'是':'否'" />

				<van-cell required title="体温值(℃)" v-if="!list.F_TemperatureIsNormal"   :value="list.F_Temperature" />


				<van-cell required title="现场人像照" v-if="this.face[0] != staticUrl">
					<template #label>
						<van-image
							@click="look(index, face)"
							style="margin-right:0.3rem;"
							v-for="(item, index) in face"
							:key="index"
							width="5rem"
							height="5rem"
							fit="contain"
							:src="item"
						/>
					</template>
				</van-cell>


				<van-cell required title="行程码" v-if="this.hand[0] != staticUrl">
					<template #label>
						<van-image
							@click="look(index, hand)"
							style="margin-right:0.3rem;"
							v-for="(item, index) in hand"
							:key="index"
							width="5rem"
							height="5rem"
							fit="contain"
							:src="item"
						/>
					</template>
				</van-cell>

				<van-cell title="备注" :label="list.F_Remark" />
			</van-cell-group>
		</div>
	</div>
</template>

<script>
import { ImagePreview, Dialog, Toast } from 'vant';
import {details,getInfo} from "@/api/house";




export default {
	name: 'detailUser',
	data() {
		return {
			isSameAddress: true,
			list:'',
			form: {
				// name: '张三',
				// phone: '18312341234',
				// idcard: '360103191212121234',
				// sex: '男',
				// nation: '汉',
				// address: '江西省永修县南山村',

				// flow: '常住',
				// follow: '是'
			},
			followPhoto: [],
			face: [],
			hand: [],
			

			headimg:[],
			photo:[]
			
			

			// F_PersonnelID:"",
			// condition:"",
			// HouseID:"",
			// haveuserTypeList:[],
			// PoliceStationlist:[],
			// F_PutFilesDateTime:"",
			// F_RemoveFilesDateTime:""
		};
	},
	created() {
		//  console.log(this.$route.query.majorparticulars);
		//  console.log(this.$route.query.F_PersonnelID);
		//  console.log(this.$route.query.condition);


		//  this.F_PersonnelID=this.$route.query.F_PersonnelID;//人员id
		//  this.condition=this.$route.query.condition;
		//  this.HouseID=this.$route.query.F_HouseID;
		//  this.personnelDossier()
		//  this.returnPivotPersonnel()


	},

	computed: {
		staticUrl() {
			//静态资源地址
			return window.staticUrl;
		}
	},
	mounted(){

		this.getInfo()
		
	},
	beforeRouteLeave(to, from, next) {
		let arr = ['none', undefined];
		if (
			(document.querySelector('.van-image-preview') &&
				document.querySelector('.van-image-preview__overlay') &&
				arr.includes(document.querySelector('.van-image-preview').style.display) &&
				arr.includes(document.querySelector('.van-image-preview__overlay').style.display)) ||
			(!document.querySelector('.van-image-preview') && !document.querySelector('.van-image-preview__overlay'))
		) {
			next(true);
		} else {
			next(false);
		}
	},
	methods: {
     getInfo(){
		let parans = {
				F_CheckInRecordID: this.$route.query.F_CheckInRecordID,
			};
        getInfo(parans).then((res) =>{
			   if(res.code == 0 ){
				  this.list =res.data;


				  this.headimg = (this.staticUrl+res.data.F_IdentityCardPhoto).split(',');

				  this.face = (this.staticUrl+res.data.F_OwnerPersonalPhoto).split(',');
				
				  this.hand = (this.staticUrl+res.data.F_TripCode).split(',');

			   }
            }).catch((err)=>{
                console.log(err)
        })
	},


		leftclick() {
			this.$router.go(-1);
		},

		// 调用拨号功能
		callPhone(phoneNumber) {
			window.location.href = 'tel:' + phoneNumber;
		},
		//初始化
		personnelDossier() {
			personnelDossier(this.F_PersonnelID, this.HouseID).then(res => {
				console.log(res);
				this.form = res.data;
				this.face = (this.staticUrl + res.data.F_OwnerPersonalPhoto).split(',');
				this.headimg = (this.staticUrl + res.data.F_IdentityCardPhoto).split(',');
				this.hand = (this.staticUrl + res.data.F_PassportPhoto).split(',');
				this.cardFace = (this.staticUrl + res.data.F_IDCardImage).split(',');
			});
		},

		look(index, images) {
			console.log(index, images);
			ImagePreview({
				images: images,
				startPosition: index
			});
		},


		edit() {
			console.log(JSON.stringify(this.form));
			
			this.$router.push({
				path: '/register/createdhotel',
				query: { condition:"emphasis",room:this.$route.query.room,F_CheckInRecordID:this.$route.query.F_CheckInRecordID,F_HotelRoomID:this.$route.query.F_HotelRoomID }
			});
		}
	}
};
</script>

<style lang="scss" scoped>
.content {
	overflow-y: scroll;
    height: calc(100% - 46px);
    padding-top: 3rem;
	.content_1 {
		display: block;
		margin: auto;
	}
}
.content {
	padding-top: 3rem;
}
.btnBox {
	display: flex;
	width: calc(100% - 2rem);
	margin-left: 1rem;
	margin-top: 1rem;
	margin-bottom: 1rem;
	justify-content: space-between;
	.btns {
		max-width: 28%;
		font-size: 0.8rem;
	}
}

::v-deep .van-cell__label {
	width: 22rem;
}
.van-button--block {
	width: 5.2rem;
}

::v-deep .fang .van-cell__value{
    text-align: left;
    flex:1.2;
    font-size: 16px;
    font-weight: bold;
    color: black;
}
</style>
